import pdfToText from "react-pdftotext";

export interface SideData {
  ordernr: number;
  deliverynr: number;
  completed: boolean;
  varer: VareData[];
}

export interface VareData {
  nr: string;
  varenr: string;
  gtin: string;
  beskrivelse: string;
  delivered: number;
  rested: number;
}

export async function parsePDF(file: File): Promise<SideData[]> {
  try {
    const pdfText = await pdfToText(file);
    if (!pdfText.includes("Megaflis AS Avd. Grossist")) {
      throw new Error("Ikke en grossist flgeseddel.");
    }
    return parsePdfData(pdfText);
  } catch (error) {
    throw error;
  }
}

const parsePdfData = (text: string): SideData[] => {
  const parsedPages: SideData[] = [];
  const pages = text
    .split(/Flgeseddel\s+/)
    .filter((section) => section.trim() !== "");

  pages.forEach((page) => {
    const ordrenummerMatch = page.match(/Bestillingsnr.\s+(\d+)/);
    const folgeseddelnummerMatch = page.match(/Flgeseddelnr.\s+(\d+)/);

    const ordernr = ordrenummerMatch ? parseInt(ordrenummerMatch[1]) : 0;
    const deliverynr = folgeseddelnummerMatch
      ? parseInt(folgeseddelnummerMatch[1])
      : 0;

    const colIndex = page.indexOf(
      "Nr.   Lev.varenr   GTIN   Beskrivelse   levert   Rest   Enhet"
    );

    const varer: VareData[] = [];
    if (colIndex !== -1) {
      const tableText = page.slice(colIndex);
      const matchingRows = tableText.match(
        /\b\d{6}(?:\s+\d{6})*[\s\S]*?(?=\b\d{6}\b\s|\bSide\b|\n|$)/g
      );
      if (matchingRows) {
        matchingRows.forEach((line) => {
          const parts = line.trim().split(/\s{2,}/);
          if (parts.length >= 6 && parts.length < 8) {
            const vare: VareData = {
              nr: parts[0],
              varenr: parts[1],
              gtin: parts[2],
              beskrivelse: parts[3],
              delivered: isNaN(Number(parts[4].trim().replace(",", ".")))
                ? 0
                : Number(parts[4].trim().replace(",", ".")),
              rested: isNaN(Number(parts[5].trim().replace(",", ".")))
                ? 0
                : Number(parts[5].trim().replace(",", ".")),
            };
            varer.push(vare);
          } else if (parts.length >= 8) {
            const vare: VareData = {
              nr: parts[0],
              varenr: parts[1],
              gtin: parts[2],
              beskrivelse: parts[3],
              delivered: isNaN(Number(parts[5].trim().replace(",", ".")))
                ? 0
                : Number(parts[5].trim().replace(",", ".")),
              rested: isNaN(Number(parts[6].trim().replace(",", ".")))
                ? 0
                : Number(parts[6].trim().replace(",", ".")),
            };
            varer.push(vare);
          }
        });
      } else {
        console.error("Ingen linjer passet");
      }
    }

    const sideData: SideData = {
      ordernr,
      deliverynr,
      completed: false,
      varer,
    };
    parsedPages.push(sideData);
  });

  return parsedPages;
};
