import { parsePDF, SideData } from "./PDFParse";
import { Link } from "react-router-dom";
import { useState, useCallback } from "react";
import { useDropzone } from "react-dropzone";
import {
  StikkontrollDisplay,
  TestStikkontrollDisplay,
} from "./StikkontrollDisplay";

export const Stikkontroll: React.FC = () => {
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [parsedPdf, setParsedPdf] = useState<SideData[] | null>(null);

  const onDrop = useCallback(async (acceptedFiles: File[]) => {
    setErrorMessage(null);
    const fil = acceptedFiles[0];

    if (fil && fil.type === "application/pdf") {
      setLoading(true);
      try {
        const pdfData = await parsePDF(fil);
        setParsedPdf(pdfData);
      } catch (error: unknown) {
        if (error instanceof Error) {
          setErrorMessage(error.message);
        } else {
          setErrorMessage("Noe gikk galt.");
        }
      } finally {
        setLoading(false);
      }
    } else {
      setErrorMessage("Dette var ikke en PDF fil nei.");
    }
  }, []);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      "application/pdf": [".pdf"],
    },
  });

  return (
    <div className="flex flex-col min-h-screen">
      <nav className="bg-mdark text-white p-8 flex">
        <div className="flex justify-between font-bold">
          <Link to="/bokforing" className="text-xl cursor-pointer">
            Bokfring
          </Link>
          <p className="text-xl px-2">/</p>
          <p className="text-xl text-myellow-500 cursor-pointer">
            Stikkontroll
          </p>
        </div>
      </nav>

      <div className="flex-grow flex items-center justify-center bg-gray-100 relative">
        <div className="flex flex-col items-center relative">
          {/* {          {loading && (
            <div className="loader ease-linear rounded-full border-8 border-t-8 border-gray-200 h-16 w-16"></div>
          )}
          {!loading && parsedPdf ? (
            <StikkontrollDisplay/>
          ) : (
            !loading && (
              <div
                {...getRootProps()}
                className={`bg-white p-28 rounded-lg w-[500px] h-64 border-4 border-dashed ${
                  isDragActive ? "border-myellow" : "border-gray-300"
                } flex items-center justify-center`}
              >
                <input {...getInputProps()} />
                <p className="text-mdark text-center font-bold">
                  {isDragActive
                    ? "Slipp!"
                    : "Dra 'Salg - Flgeseddel.pdf' hit :-) Eller trykk her for  velge pdf selv"}
                </p>
              </div>
            )
          )}} */}
          <TestStikkontrollDisplay />
          {errorMessage && (
            <p className="mt-4 text-red-500 text-center font-bold absolute bottom-[-70px]">
              Feilmelding: {errorMessage}
            </p>
          )}
        </div>
      </div>
      <footer className="bg-mdark text-white p-8" />
    </div>
  );
};
