import { useEffect, useRef, useState } from "react";
import { SideData } from "./PDFParse";
import { Tab, TabGroup, TabList, TabPanel, TabPanels } from "@headlessui/react";
import { FaUndo } from "react-icons/fa";

export const DataDisplay: React.FC<{ data: SideData[] }> = ({ data }) => {
  const tabListRef = useRef<HTMLDivElement>(null);
  const copyRefs = useRef<{ [key: string]: HTMLParagraphElement | null }>({});
  const [isDragging, setIsDragging] = useState(false);
  const [startX, setStartX] = useState(0);
  const [scrollLeft, setScrollLeft] = useState(0);
  const [showList, setShowList] = useState<boolean>(false);
  const [tabIndex, setTabIndex] = useState<number>(0);
  const [currentData, setCurrentData] = useState<SideData[]>(data);

  const handleMouseDown = (e: React.MouseEvent) => {
    const tabList = tabListRef.current;
    if (tabList) {
      setIsDragging(true);
      setStartX(e.pageX - tabList.offsetLeft);
      setScrollLeft(tabList.scrollLeft);
    }
  };
  const handleMouseMove = (e: MouseEvent) => {
    const tabList = tabListRef.current;
    if (!isDragging || !tabList) return;
    const x = e.pageX - tabList.offsetLeft;
    tabList.scrollLeft = scrollLeft - (x - startX);
  };
  const handleMouseUp = () => {
    setIsDragging(false);
  };
  const handleNext = () => {
    setCurrentData((prevData) => {
      const updatedData = prevData.map((item, index) => {
        if (index === tabIndex) {
          return { ...item, completed: true };
        }
        return item;
      });
  
      return updatedData;
    });
      setTabIndex((prevTabIndex) => {
      const newTabIndex = prevTabIndex < currentData.length - 1 ? prevTabIndex + 1 : prevTabIndex;
      return newTabIndex;
    });
  };
  const handleUndo = () => {
    setTabIndex((prevTabIndex) => {
      const newTabIndex = prevTabIndex > 0 ? prevTabIndex - 1 : 0;
        setCurrentData((prevData) => {
        const updatedData = prevData.map((item, index) => {
          if (index === newTabIndex) {
            return { ...item, completed: false };
          }
          return item;
        });
  
        return updatedData;
      });
  
      return newTabIndex;
    });
  };
    useEffect(() => {
    if (isDragging) {
      window.addEventListener("mousemove", handleMouseMove);
      window.addEventListener("mouseup", handleMouseUp);
    } else {
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("mouseup", handleMouseUp);
    }
    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("mouseup", handleMouseUp);
    };
  }, [isDragging]);
  const copyText = (key: string) => {
    const ref = copyRefs.current[key];
    if (ref) {
      navigator.clipboard.writeText(ref.innerText);
    }
  };
  return (
    <div className="p-6 bg-mdark rounded-lg w-[1200px] h-[650px] text-white shadow-lg flex flex-col">
      <TabGroup
        selectedIndex={tabIndex}
        onChange={setTabIndex}
        className="flex flex-col h-full"
      >
        <TabList
          ref={tabListRef}
          onMouseDown={handleMouseDown}
          className="flex space-x-1 p-1 overflow-x-auto tab-scrollbar pb-2.5"
        >
          {currentData.map((item, index) => (
            <Tab
              key={index}
              onMouseDown={handleMouseDown}
              className={` text-mdark py-1 px-7 font-bold rounded-sm ${
                item.completed
                  ? "bg-green-500 aria-selected:bg-green-700 hover:bg-green-400"
                  : "bg-myellow-500 aria-selected:bg-myellow-600 hover:bg-yellow-400"
              }`}
            >
             <p className="text-sm">{index+1 + "/" + currentData.length}</p>
             <p className="">{item.ordernr}</p>
            </Tab>
          ))}
        </TabList>

        <TabPanels className="flex-grow mt-2">
          {currentData.map((item, index) => (
            <TabPanel
              key={index}
              className="pt-4 pr-4 pl-4 border border-myellow-500/20 rounded h-full flex flex-col"
            >
              <div className="flex justify-between">
                
                <div className="flex">
                <p className="w-32 flex-shrink-0">Bestillingsnr.</p>
                <p ref={(c) => (copyRefs.current['bestillingsnr'] = c)} onClick={() => copyText('bestillingsnr')} className="bg-mbeige/20 font-medium px-1 border border-myellow-500/20 text-nowrap cursor-copy active:border-myellow-200 mr-2 select-none">
                    {item.ordernr}
                  </p>
                  <p className="font-medium mr-2">Flgeseddelnr.</p>
                  <p ref={(c) => (copyRefs.current['folgeseddel'] = c)} onClick={() => copyText('folgeseddel')} className="bg-mbeige/20 font-medium px-1 border border-myellow-500/20 text-nowrap cursor-copy active:border-myellow-200 select-none">
                    {item.deliverynr}
                  </p>
                </div>
                <div className="flex items-center">
                  <input
                    onChange={() => setShowList(!showList)}
                    type="checkbox"
                    checked={showList}
                    id="visliste"
                    className=""
                  />
                  <label htmlFor="visliste" className="ml-1 select-none">
                    Vis hele listen
                  </label>
                  <FaUndo onClick={handleUndo} className="text-red-700 ml-2 hover:text-red-500 active:text-red-400 cursor-pointer select-none" />
                  <button
                    onClick={handleNext}
                    className="bg-yellow-500 hover:bg-myellow-400 active:bg-myellow-600 ml-1 px-2 font-medium text-mdark rounded-sm b"
                  >
                    Bokfrt
                  </button>
                </div>
              </div>
              <div className="flex mt-2 overflow-auto no-scrollbar">
                <p className="font-medium w-32 flex-shrink-0">Filter</p>

                <p
                  ref={(c) => (copyRefs.current['filter'] = c)}
                  onClick={() => copyText('filter')}
                  className="bg-mbeige/20 text-nowrap font-medium px-1 border border-myellow-500/20 flex-grow overflow-auto no-scrollbar whitespace-nowrap cursor-copy active:border-myellow-200 "
                >
                  {item.varer
                    .filter(
                      (v) =>
                        v.delivered === 0 || (v.delivered > 0 && v.rested > 0)
                    )
                    .map((v) => v.nr)
                    .join("|") || "Ingen rest igjen. Bokfr ordre og FM :-)"}
                </p>

              </div>
              <div className="mt-5 overflow-y-auto flex-grow max-h-[400px] tab-scrollbar text-nowrap">
                <table className="min-w-max w-full table-auto">
                  <thead className="text-base ">
                    <tr>
                      <th className="text-left w-16">Nr.</th>
                      <th className="text-left w-56">Lev.varenr</th>
                      <th className="text-left w-[120px]">GTIN</th>
                      <th className="text-left w-[600px]">Beskrivelse</th>
                      <th className="text-center ">Levert</th>
                      <th className="text-center ">Rest</th>
                    </tr>
                  </thead>
                  <tbody className="text-sm break-words">
                    {!showList
                      ? item.varer
                          .filter(
                            (v) =>
                              v.delivered === 0 ||
                              (v.delivered > 0 && v.rested > 0)
                          )
                          .sort((a, b) => {
                            if (
                              a.delivered > 0 &&
                              a.rested > 0 &&
                              !(b.delivered > 0 && b.rested > 0)
                            )
                              return -1;
                            if (
                              b.delivered > 0 &&
                              b.rested > 0 &&
                              !(a.delivered > 0 && a.rested > 0)
                            )
                              return 1;
                            return 0;
                          })
                          .map((vare, vareIndex) => (
                            <tr
                              key={vareIndex}
                              className={`odd:bg-mblue-600/60 ${
                                vare.delivered > 0 && vare.rested > 0
                                  ? "text-green-500 font-medium"
                                  : ""
                              }`}
                            >
                              <td className="">{vare.nr}</td>
                              <td className="">{vare.varenr}</td>
                              <td className="">{vare.gtin}</td>
                              <td className="">{vare.beskrivelse}</td>
                              <td
                                className={`text-center ${
                                  vare.delivered > 0 && vare.rested > 0
                                    ? "text-orange-500/80"
                                    : ""
                                }`}
                              >
                                {vare.delivered}
                              </td>
                              <td className="text-center">{vare.rested}</td>
                            </tr>
                          ))
                      : item.varer.map((vare, vareIndex) => (
                          <tr key={vareIndex} className="odd:bg-mblue-600/60">
                            <td className="">{vare.nr}</td>
                            <td className="">{vare.varenr}</td>
                            <td className="">{vare.gtin}</td>
                            <td className="">{vare.beskrivelse}</td>
                            <td className=" text-center">{vare.delivered}</td>
                            <td className=" text-center">{vare.rested}</td>
                          </tr>
                        ))}
                  </tbody>
                </table>
              </div>
            </TabPanel>
          ))}
        </TabPanels>
      </TabGroup>
    </div>
  );
};
