import React from "react";

import { HashRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import { Bokforing } from "./Bokforing";
import { Stikkontroll } from "./Stikkontroll";

const App: React.FC = () => {
  return (
    <Router basename="/">
      <Routes>
        <Route path="/" element={<Navigate to="/bokforing" />} />
        <Route path="/bokforing" element={<Bokforing />} />
        <Route path="/stikkontroll" element={<Stikkontroll />} />
      </Routes>
    </Router>
  );
};

export default App;
